<template>
<div class="itemBox">
<ul>
  <li> : {{itemprop.itemName}}</li>
  <li>  : {{itemprop.quantity}}</li>
  <li>  : {{itemprop.price}}</li>
</ul>
  <v-btn color="success" v-on:click="orderList"></v-btn>
  <v-btn color="warning" v-on:click="deleteList"></v-btn>
</div>

</template>

<script>
export default {
  name: "Item",
  props : ['itemprop'],
  methods : {
    deleteList()
    {
      this.$emit('delete',this.itemprop.id)
    },
    orderList()
    {
      this.$router.push({name :"Order",query :{itemId : this.itemprop.id}} )
    }
  }
}
</script>

<style scoped>
.itemBox{
  display: flex;
}
</style>